import "./IntroText.css";

export default function IntroText() {
  return (
    <div className="intro">
      <h2 className="h2-intro">Minebikers MCC esittytyy:</h2>
      <p className="p-intro">
        Minebikers on Vantaan Kaivokselassa vuonna 2000 perustettu ja edelleen
        toimiva moottoripyrharrastajien kerho. Emme ole yhdistysrekisteriss,
        mutta kevll 2023 haimme kerholle ja sen vreille hyvksynnn Biker
        Meetingiss. Kerhomme jsenill ja jsenyytt haluavilla tulee olla
        vhintn 500-kuutioinen moottoripyr tai ainakin aikomus hankkia
        sellainen. Merkill ei ole vli, toki Suzukin hankkineet joutuvat
        selittelemn valintaansa vhemmn ja isoon Intruderiin ptyneet saavat
        suitsutusta parhaasta mahdollisesta valinnasta.
      </p>
      <p className="p-intro">
        Parhaimmillaan 2000-luvun alkuvuosina jseni oli yli 20, mutta aika ja
        evoluutio ovat karsineet vanhemmasta pst porukkaa sohvan helln
        huomaan. Aktiivisten jsenten mr on tll hetkell 12 henkil ja
        parhaimmillaan ajeluihin on saatu lhes kaikki liikekannalle.
      </p>
      <p className="p-intro">
        Vuosittaisena ptapahtumana on alusta alkaen ollut Viron Jgevassa
        hein-elokuun vaihteessa jrjestettv Jgevatreff. Tm lajinsa
        suurimpaan tapahtumiin euroopassa kuuluva jotos on vetnyt
        motoristejamme jo vuosikymmenten ajan, kokeneimmilla jsenill on
        takanaan jo parisenkymment suoritusta vaikka vlivuosia on vkisinkin
        kaikille tullut. Eik ihme, sill mik voisi voittaa huonosti nukuttua
        viikonloppua ja suurella todennkisyydell kastumista joko teltassa tai
        viimeistn paluumatkan vesisateessa...
      </p>
    </div>
  );
}
